import { Injectable } from "@angular/core";
import { Http, Response, RequestOptions, Headers,URLSearchParams } from '@angular/http';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import { UserDetailResp } from "./userdetailresp.model";



@Injectable()
export class UserDetailService {

    appHost : string;

    constructor( private http: Http){
        this.appHost = "http://localhost:8080"

    }
    pauseAction(input:string) :Observable<UserDetailResp>{
       

        let myHeaders = new Headers();
        myHeaders.append('Content-Type', 'application/json');    
      //  let myParams = new URLSearchParams();
      //  myParams.set('delayInput', input);	
      
        let options = new RequestOptions({ headers: myHeaders });
       // options.params = myParams;
        return this.http.get(this.appHost+'/userDetailsDelay'+input,  options).map(res => res.json());
    }
  
    outOfMemory(){

        let myHeaders = new Headers();
        myHeaders.append('Content-Type', 'application/json');    
       
        let options = new RequestOptions({ headers: myHeaders });
        
        return this.http.get(this.appHost+'/userDetailsOutMemory',options);
    }
  
    userDetails() :Observable<UserDetailResp>{

        let myHeaders = new Headers();
        myHeaders.append('Content-Type', 'application/json');    
       
        let options = new RequestOptions({ headers: myHeaders });

        console.log("  frpm service")
        
        return this.http.get(this.appHost+'/userDetails',options).map(res =>
        res.json());
    }

    thread(){

        let myHeaders = new Headers();
        myHeaders.append('Content-Type', 'application/json');    
       
        let options = new RequestOptions({ headers: myHeaders });
        
        return this.http.get(this.appHost+'/deadlock',options);
    }

    thread1(){

        let myHeaders = new Headers();
        myHeaders.append('Content-Type', 'application/json');    
       
        let options = new RequestOptions({ headers: myHeaders });
        
        return this.http.get(this.appHost+'/deadlock1',options);
    }

    deadLockInfo(){

        let myHeaders = new Headers();
        myHeaders.append('Content-Type', 'application/json');    
       
        let options = new RequestOptions({ headers: myHeaders });
        
        return this.http.get(this.appHost+'/deadLockInfo',options);
    }
}


