import { Component } from '@angular/core';
import { UserDetailService } from './user.details.service';
import { UserDetailResp } from './userdetailresp.model';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'TechVision';

  pauseInput : string ;

  userResp : UserDetailResp;

  showDetails :boolean;

  constructor(private userService : UserDetailService){
    this.showDetails = false;
  }

  pauseAction(){
  this.userService.pauseAction(this.pauseInput).subscribe(data => {
  this.userResp = data;
  }, error => {
    console.log('Error');
});


  }

  outOfMemory(){
    this.userService.outOfMemory().subscribe(data => {
      
      }, error => {
        console.log('Error');
    });
  }

  userDetails(){
    this.userService.userDetails().subscribe(data => {
      this.showDetails = true;
      this.userResp = data;

      console.log('data'+JSON.stringify(data));
      }, error => {
        console.log('Error');
    });
  }

  thread(){
    this.userService.thread().subscribe(data => {
      
      }, error => {
        console.log('Error');
    });
  }

  thread1(){
    this.userService.thread1().subscribe(data => {
      
      }, error => {
        console.log('Error');
    });
  }
  deadLockInfo(){
    this.userService.deadLockInfo().subscribe(data => {
      
      }, error => {
        console.log('Error');
    });
  }
  
}



